.header {
  color: var(--white);
  padding: 30px 20px;
  position: fixed;
  width: 100%;
  top: -111px;
  left: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  transition: 0.3s ease all;
  z-index: 3;
}
.header.border {
  border: none;
}
.header.active {
  top: 0px;
}
.header.bg {
  background: #ffffff;
  color: var(--black);
}
.header.bg .header__burger::after, .header.bg .header__burger::before {
  content: "";
  background-color: var(--black);
}
.header.bg .header__burger span::after, .header.bg .header__burger span::before {
  background-color: var(--black);
}
.header.bg .header__logo a svg path {
  transition: 0.3s ease all;
  fill: #464646;
}
.header.bg .header__switcher {
  color: rgba(70, 70, 70, 0.4);
}
.header.bg .header__switcher a {
  transition: 0.3s ease all;
}
.header.bg .header__switcher a:hover {
  color: var(--black);
}
.header.bg .header__switcher a:nth-child(1) {
  position: relative;
}
.header.bg .header__switcher a:nth-child(1)::before {
  content: "";
  top: 0;
  right: -18px;
  background-color: var(--black);
}
.header.bg .header__switcher a.active {
  color: var(--black);
}
.header.bg .header__choose {
  background: var(--white);
  color: var(--gold);
  border-color: var(--gold);
}
.header.bg .header__choose svg path {
  fill: var(--gold);
}
.header.bg .header__choose:hover {
  background: var(--gold-gradient);
  border: none;
  color: var(--white);
}
.header.bg .header__choose:hover svg path {
  fill: var(--white);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 35px;
}
.header__wrapper {
  flex: 0 0 40%;
}
.header__burger {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: flex-end;
  font-size: 13px;
  font-weight: 300;
  letter-spacing: 4%;
  text-transform: uppercase;
  line-height: 1.4;
