@media (max-width: 767px) {
  .design {
    position: relative;
    margin: 100px 0px 0px 0px;
  }
  .design__bg-title {
    position: absolute;
    top: -180px;
    right: -150%;
    white-space: nowrap;
  }
  .design__title {
    margin: 24px 0px 48px 0px;
    max-width: inherit;
  }
  .design__advantages {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
  .design__advantage_item {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 24px;
    padding-bottom: 32px;
    border-bottom: 1px solid rgba(122, 94, 72, 0.2);
  }
  .design__advantage_item:not(:last-child) {
    border-bottom: 1px solid rgba(122, 94, 72, 0.2);
  }
  .design__advantage_item-num {
    font-size: 16px;
    font-weight: 400;
    color: var(--gold);
  }
  .design__advantage_item-name {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.2;
    color: var(--brown);
    max-width: 341px;
    margin-left: -170px;
    margin-top: -10px;
  }
  .design__advantage_item-list {
    display: flex;
    flex-direction: column;
    gap: 24px;
    font-size: 18px;
    font-weight: 400;
    color: var(--black);
    line-height: 1.4;
    max-width: 539px;
    grid-column-start: 1;
    grid-column-end: 3;
    order: 4;
  }
  .design__advantage_item-list ul {
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-weight: 400;
    line-height: 1.4;
  }
  .design__advantage_item-list ul li {
    display: flex;
    align-items: center;
    gap: 12px;
  }
  .design__advantage_item-image {
    grid-column-start: 1;
    grid-column-end: 3;
    order: 3;
  }
  .design__advantage_item-image img {
    width: 100%;
  }
}
